import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import SignupConfirmation from './SignupConfirmation';
import logo from "../assests/images/logo.jpg"
import { useFormik } from 'formik'
import * as yup from 'yup'

const Register=()=>{
    const [signupConfirmation, setsignupConfirmation] = useState(false);
  
      const formik = useFormik({
          initialValues : {
              email:'',
              password:'',
          },
          validationSchema:yup.object({
            
            email:yup.string()
              .email("Enter a valid email")
              .required("Email Cannot be empty"),
             
            password:yup.string()
              .min(6 , "Minimum 6 characters required")
              .required("Password Cannot be empty"),
          }),
          onSubmit:(userInputData)=> {
            setsignupConfirmation(!signupConfirmation);
          }
      })
        return (
            <>
                <div className="h-100 d-flex flex-column">
                    {signupConfirmation ? <SignupConfirmation/> :
                    <div className="flx_1 register_panel p-4 w-100 flex-column justify-content-center d-flex">
                        <header className="text-center pb-3">
                            <img src={logo} className="img_w2 pb-3" alt="" />
                            <h3>Make the most of your professional life </h3>
                        </header>
                        <div className="register_form p-4">
                            <div className="reg_form">
                                <form onSubmit={formik.handleSubmit}>
                                <div className="pb-3">
                                    <label className="label_2">Email</label>
                                    <input type="email" 
                                    className="text_box_3"
                                    name="email"
                                    onChange={formik.handleChange}
                                    onBlur={formik.handleBlur}
                                    value={formik.values.email} />
                                {formik.touched.email && formik.errors.email ?
                                    <div className="text-danger errorMsg">{formik.errors.email}</div>
                                    : null
                                } 
                                </div>
                                <div className="pb-3">
                                    <label className="label_2">Password (6 or more characters)</label>
                                    <input type="password" className="text_box_3"
                                       name="password"
                                       onChange={formik.handleChange}
                                       onBlur={formik.handleBlur}
                                       value={formik.values.password} />
                                   {formik.touched.password && formik.errors.password ?
                                       <div className="text-danger errorMsg">{formik.errors.password}</div>
                                       : null
                                   }
                                </div>
                                <div className="pb-2">
                                    <p className="t_size_1 text-center">By clicking Agree &amp; Join, you agree to the CareerIn
                                    <a href="#/" className="txt_1">User Agreement</a>,
                                    <a href="#/" className="txt_1">Privacy Policy</a> and 
                                    <a href="#/" className="txt_1"> Cookie Policy.</a> </p>
                                </div>
                                <div className="position-relative pt-2">
                                    <input type="submit"  className="btn btn_color_2 w-100 bradius_1 btn_1" defaultValue="Agree & Join" />
                                </div>
                                </form>
                               
                            </div>
                        </div>
                    </div>
                    }
                </div>
            </>
        );
    }


export default Register;