import React, { Component } from 'react';
import FindProfile from './FindProfile';
import UploadFindBtn from './UploadFindBtn';
import UploadProfile from './UploadProfile';

class DashboardMain extends Component {
    constructor(props) {
        super(props);
        this.state={
            findProfile:false,
            uploadProfile:false
        }
    }
    
    handleFindProfile=()=>{
        this.setState({findProfile:true,uploadProfile:false})
    }
    handleUploadProfile=()=>{
        this.setState({uploadProfile:true,findProfile:false})
    }
    render() {
        return (
            <>
                <UploadFindBtn state={this.state} findProfile={this.handleFindProfile} uploadProfile={this.handleUploadProfile}/>
                 
                 {this.state.findProfile ? 
                <FindProfile/>: null
                }
                 {this.state.uploadProfile ?  
                <UploadProfile uploadProfile={this.handleUploadProfile}/> : null
                 }
            </>
        );
    }
}

export default DashboardMain;