import React, { useState } from 'react'
import cvImg from "../assests/images/upload_cv.png"
import ExcelUpload from './ExcelUpload'
import { useFormik } from 'formik'
import * as yup from 'yup'
import { Upload,message} from 'antd'


const UploadProfile = (props) => {
  const [individualUpload, setIndividualUpload] = useState(false);
  const [excelUpload, setExcelUpload] = useState(false);
  const [upload,setUpload] = useState('');
  const [uploadValidation,setUploadValidation] = useState('');

  const handleIndividualUpload = () => {
    setIndividualUpload(true)
    setExcelUpload(false)
  }
  const handleExcelUpload = () => {
    setExcelUpload(true)
    setIndividualUpload(false)
  }

  
  const onChange=(info)=> {
      if (info.file.status !== 'uploading') {
        console.log(info.file, info.fileList);
        setUpload(info)
      }
      if (info.file.status === 'done') {
        message.success(`${info.file.name} file uploaded successfully`);
        setUpload(info)
      } else if (info.file.status === 'error') {
        message.error(`${info.file.name} file upload failed.`);
      }
    }

  const formik = useFormik({
    initialValues: {
      fullname: '',
      positiontitle: '',
      phonenumber: '',
      emailid: '',
      city: '',
      state: '',
    },
    validationSchema: yup.object({

      fullname: yup.string()
        .required('Full cannot be empty'),

      positiontitle: yup.string()
        .required('Password cannot be empty'),

      phonenumber: yup.string()
        .required('Phone number cannot be empty'),
      
      emailid: yup.string()
        .email('Enter a valid Email')
        .required('Phone number cannot be empty'),

      city: yup.string()
        .required('City cannot be empty'),

      state: yup.string()
        .required('State cannot be empty'),
     
    }),
    onSubmit: (userInputData) => {
      if(upload === ''){
          setUploadValidation("Please Upload your attachments")
      }else{
          alert("Form Submitted")
    }
    }
  })

  return (
    <div>
      <div className="upload_row pt-3 pb-3">
        <div className="row">
          <div className="col-md-6 text-center text-md-right">
            <span>
              <input type="radio" name={1} onClick={handleIndividualUpload} />
            </span>
            <label className="pl-2">Individual upload</label>
          </div>
          <div className="col-md-6 text-center text-md-left">
            <div className="custom-control custom-radio custom-control-inline">
              <span>
                <input type="radio" name={1} onClick={handleExcelUpload} />
              </span>
              <label className="pl-2">Excel upload</label>
            </div>
          </div>
        </div>
      </div>

      {individualUpload ?
      <form onSubmit={formik.handleSubmit}>
        <div className="row">
          <div className="col-md-8">
            <div className="update_prodile panel_1 brad_1 p-4">
                <div className="pb-3">
                  <label>Full name</label>
                  <input type="text"
                   className="text_box_3" 
                    name="fullname"
                    placeholder="Full Name"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.fullname}
                   />
                  {formik.touched.fullname && formik.errors.fullname ?
                    <div className="text-danger errorMsg">{formik.errors.fullname}</div>
                    : null
                  }
                </div>
                <div className="pb-3">
                  <label>Position Title</label>
                  <input type="text"
                   className="text_box_3"
                   name="positiontitle"
                   placeholder="Position Title"
                   onChange={formik.handleChange}
                   onBlur={formik.handleBlur}
                   value={formik.values.positiontitle}
                   />
                   {formik.touched.positiontitle && formik.errors.positiontitle ?
                    <div className="text-danger errorMsg">{formik.errors.positiontitle}</div>
                    : null
                  }
                </div>
                <div className="pb-3">
                  <label>Phone Number</label>
                  <input type="text" 
                  className="text_box_3" 
                  name="phonenumber"
                  placeholder="Phone Number"
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.phonenumber}
                  />
                  {formik.touched.phonenumber && formik.errors.phonenumber ?
                    <div className="text-danger errorMsg">{formik.errors.phonenumber}</div>
                    : null
                  }
                </div>
                <div className="pb-3">
                  <label>Email Id</label>
                  <input type="text" 
                  className="text_box_3" 
                  name="emailid"
                  placeholder="Email Id"
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.emailid}
                  />
                  {formik.touched.emailid && formik.errors.emailid ?
                    <div className="text-danger errorMsg">{formik.errors.emailid}</div>
                    : null
                  }
                </div>
                <div className="pb-3">
                  <label>City</label>
                  <input type="text" 
                  className="text_box_3" 
                  name="city"
                  placeholder="City"
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.city}
                  />
                  {formik.touched.city && formik.errors.city ?
                    <div className="text-danger errorMsg">{formik.errors.city}</div>
                    : null
                  }
                </div>
                <div className="pb-3">
                  <label>State</label>
                  <input type="text" 
                  className="text_box_3" 
                  name="state"
                  placeholder="State"
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.state}
                  />
                  {formik.touched.state && formik.errors.state ?
                    <div className="text-danger errorMsg">{formik.errors.state}</div>
                    : null
                  }
                </div>
                <div className="position-relative pt-2">
                  <input type="submit" className="btn btn_color_2 w-100 bradius_1" defaultValue="Submit" />
                </div>
            </div>
          </div>
          <div className="col-md-4">
            <div className="upload_cv text-center">
              <figure>
                <img src={cvImg} alt="" />
              </figure>
              <Upload
              onChange={onChange}
              multiple={false}
              >
             <div className="col-md-12">
              <span className="btn btn-secondary w-100 btn-file">
                Attach <input type="file" />
              </span>
              </div>
              </Upload>
              {upload === '' ? 
                    <div className="text-danger errorMsg">{uploadValidation}</div>
                    : null 
                  }
            </div>
          </div>
        </div> 
        </form>
        : null}

      {excelUpload ?
        <ExcelUpload /> :
        null}

    </div>

  )
}
export default UploadProfile;