import React, { Component } from 'react';
import Header from './Header';
import { useFormik } from 'formik'
import * as yup from 'yup'

const ForgetPassword=(props)=>{
    const formik = useFormik({
        initialValues: {
            email: '',
        },
        validationSchema: yup.object({

            email: yup.string()
                .email('Enter a valid email')
                .required('Email cannot be empty'),

           
        }),
        onSubmit: (userInputData) => {
            alert(userInputData.email + " " + "Form Submitted....")
        }
    })

    
        return (
            <>
                    {/* Forgot Password */}
                    <div className="flx_1 w-100 pt-2 pt-lg-5 justify-content-center d-flex">
                        <div className="forget_panel p-lg-0 p-3">
                            <div>
                                <h3>Forgot Password</h3>
                                <h6>Please enter your email</h6>
                                <div className="login_form pt-3">
                                <form onSubmit={formik.handleSubmit}>
                                        <div className="position-relative pb-3">
                                        <label>Email *</label>
                                        <input type="email"
                                         className="text_box_2 text-left bradius_0"
                                          placeholder="Enter your email"
                                          id="email"
                                          name="email" 
                                          onChange={formik.handleChange}
                                          onBlur={formik.handleBlur}
                                          value={formik.values.email} />
                                      {formik.touched.email && formik.errors.email ?
                                          <div className="text-danger errorMsg">{formik.errors.email}</div>
                                          : null
                                      }
                                          
                                    </div>
                                    <div className="position-relative pt-4">
                                        <input type="button" defaultValue="Cancel" onClick={props.cancel} className="btn btn-outline-primary bradius_0 pl-4 pr-4" />
                                        <input type="submit" defaultValue="Submit" className="btn btn_color_1 bradius_0 pl-4 pr-4" />
                                    </div>
                                    </form>
                                </div>
                                
                            </div>
                        </div>
                    </div>
            </>
        );
    }


export default ForgetPassword;