import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import ForgetPassword from './ForgetPassword';
import { useFormik } from 'formik'
import * as yup from 'yup'
import Header from './Header';
import { Spin } from 'antd';

const Signin = (props) => {
    const [forgotPasswordModule, setforgotPasswordModule] = useState(false);
    const [show, setShow] = useState(false)
    const [loading, setLoading] = useState(false);

    const showPassword = () => {
        setShow(!show)
    }
    const handleForgotPassword = () => {
        setforgotPasswordModule(!forgotPasswordModule);
    };
    const helloHandeler = () => {
        setLoading(true)
        setTimeout(() => {
            setLoading(false)
            props.history.push("/dashboard")
        }, 2000)

    };
    const formik = useFormik({
        initialValues: {
            email: '',
            password: '',
        },
        validationSchema: yup.object({

            email: yup.string()
                .email('Enter a valid email')
                .required('Email cannot be empty'),

            password: yup.string()
                .min(6, "Minimum 6 characters required")
                .required('Password cannot be empty'),
        }),
        onSubmit: (userInputData) => {
            helloHandeler();

        }
    })
    const spin = {
        textAlign: 'center',
        background: 'rgba(0, 0, 0, 0.05)',
        borderRadius: '4px',
        marginBottom: '20px',
        padding: "48Vh",
        margin: '0'
    }

    return (
        <>
            {loading ?
                <div style={spin}>
                    <Spin spinning={loading} delay={500} />
                </div> :

                <div className="d-flex flex-column h-100">
                    {forgotPasswordModule ?
                        <>
                            <Header />
                            <ForgetPassword cancel={handleForgotPassword} />

                        </> :
                        <div className="flx_1 w-100 align-items-center justify-content-center d-flex">
                            <div className="login_panel p-lg-0 p-3">
                                <h4 className="txt_600 text-center">Welcome Back</h4>
                                <p className="txt_color_1 text-center">Don't miss your next opportunity. Sign in to stay updated on your professional world.</p>
                                <div className="login_form pt-4 ">
                                    <form onSubmit={formik.handleSubmit}>
                                        <div className="position-relative pb-3">
                                            <input type="email"
                                                id="email"
                                                name="email"
                                                className="text_box text-left"
                                                placeholder="Email"
                                                onChange={formik.handleChange}
                                                onBlur={formik.handleBlur}
                                                value={formik.values.email} />
                                            {formik.touched.email && formik.errors.email ?
                                                <div className="text-danger errorMsg">{formik.errors.email}</div>
                                                : null
                                            }
                                        </div>
                                        <div className="position-relative">
                                            <input type={show ? "text" : "password"}
                                                className="text_box text-left"
                                                placeholder="Password"
                                                id="password"
                                                name="password"
                                                onChange={formik.handleChange}
                                                onBlur={formik.handleBlur}
                                                value={formik.values.password} />
                                            {formik.touched.password && formik.errors.password ?
                                                <div className="text-danger errorMsg">{formik.errors.password}</div>
                                                : null
                                            }

                                            <span className="show_txt" onClick={showPassword}>{show ? "Hide" : "Show"}</span>
                                        </div>
                                        <div className="position-relative text-center pt-4">
                                            <input type="submit" className="btn btn_color_1 w-100 bradius_1 btn_1" value="signin" />
                                        </div>
                                    </form>
                                </div>
                                <div className="pt-4 text-center">
                                    <a href="#/" onClick={() => handleForgotPassword()} className="btn_2">Forget Password?</a>
                                </div>
                                <div className="pt-3 text-center">
                                    <span>New to CareerIn?</span>
                                    <Link to="/register" className="btn_3 pl-1">Join now</Link>
                                </div>
                            </div>
                        </div>
                    }
                </div>
            }
        </>
    );
}


export default Signin;